body {
  height: 100vh;
  justify-content: center;
  align-items: center;
  display: flex;
}

.calculator {
  width: 25%;
  text-align: center;
  background-color: #fff;
  border: 1px solid #ccc;
  border-radius: 5px;
  align-items: center;
  padding: 20px;
}

input {
  width: 100%;
  text-align: right;
  box-sizing: border-box;
  margin-bottom: 10px;
  padding: 5px;
}

.container {
  grid-template-columns: repeat(4, 1fr);
  gap: 5px;
  display: grid;
}

button {
  color: #fff;
  cursor: pointer;
  background-color: #519f73;
  border: none;
  border-radius: 5px;
  padding: 10px;
}

button:hover {
  background-color: #16670d;
}

.operator {
  background-color: #dc8c46;
}

.operator:hover {
  background-color: #b8b8b8;
}

.btn-clear {
  background-color: #dc3545;
  grid-column: span 3;
}

.btn-equal {
  background-color: #dc3545;
  grid-column: span 2;
}

.btn-clear:hover, .btn-equal:hover {
  background-color: #c82333;
}

